import { useRef, useEffect } from 'react';
import 'plyr/dist/plyr.css';
import { Box, Text, Badge, Group, Stack } from '@mantine/core';

interface VideoPlayerProps {
  fileId: number;
  filename: string;
  quality?: number;
  resolution?: string;
  codec?: string;
  showMetadata?: boolean;
}

export default function VideoPlayer({
  fileId,
  filename,
  quality,
  resolution,
  codec,
  showMetadata = true
}: VideoPlayerProps) {
  const videoRef = useRef<HTMLVideoElement>(null);
  const playerRef = useRef<any>(null);
  const apiBase = (import.meta.env.VITE_API_BASE_URL || '').replace(/\/$/, '');

  useEffect(() => {
    let cancelled = false;

    const setupPlayer = async () => {
      if (!videoRef.current || playerRef.current) return;
      const PlyrModule: any = await import('plyr');
      if (cancelled) return;
      const PlyrClass = PlyrModule.default ?? (PlyrModule as any);
      playerRef.current = new PlyrClass(videoRef.current, {
        controls: [
          'play-large',
          'play',
          'progress',
          'current-time',
          'duration',
          'mute',
          'volume',
          'settings',
          'pip',
          'airplay',
          'fullscreen'
        ],
        settings: ['quality', 'speed', 'loop'],
        quality: {
          default: 1080,
          options: [4320, 2880, 2160, 1440, 1080, 720, 576, 480, 360, 240]
        },
        speed: {
          selected: 1,
          options: [0.5, 0.75, 1, 1.25, 1.5, 1.75, 2]
        },
        ratio: '16:9',
        storage: { enabled: true, key: 'mediavault-player' }
      });
    };

    setupPlayer();

    return () => {
      cancelled = true;
      if (playerRef.current) {
        playerRef.current.destroy();
        playerRef.current = null;
      }
    };
  }, [fileId]);

  const getQualityColor = (score?: number): string => {
    if (!score) return 'gray';
    if (score >= 150) return 'green';
    if (score >= 100) return 'blue';
    if (score >= 50) return 'yellow';
    return 'red';
  };

  return (
    <Stack gap="sm">
      {showMetadata && (
        <Group gap="xs">
          <Text size="sm" fw={600}>{filename}</Text>
          {quality !== undefined && (
            <Badge color={getQualityColor(quality)} variant="light">
              Q: {quality}
            </Badge>
          )}
          {resolution && (
            <Badge color="blue" variant="outline">{resolution}</Badge>
          )}
          {codec && (
            <Badge color="gray" variant="outline">{codec}</Badge>
          )}
        </Group>
      )}
      <Box>
        <video
          ref={videoRef}
          crossOrigin="anonymous"
          playsInline
          controls
        >
          <source
            src={`${apiBase}/api/stream/${fileId}`}
            type="video/mp4"
          />
        </video>
      </Box>
    </Stack>
  );
}
